<template lang="html">
  <div>
    <con-doc  titlex="Getting Started" textx="Vuesax es una libreria de componentes de vue js que te facilita el frontend y agiliza el trabajo con una gran calidad visual.">
      <div slot="codes">
      <codex titlex="Introduccion" doc="doc" :no-codigo="true" >
        <div slot="ejemplo">
          <div class="con-code">
            Vuesax es una libreria de frontend basada en vue js que te ayuda a desarrollar mas rapido y con un impacto visual en pocos pasos esta en proceso de crecimiento y progreso.
          </div>
        </div>
      </codex>
      <codex titlex="Quick-start CDN" doc="doc" :code="code1" >
        <div slot="ejemplo">
          <div class="con-code">
            Inicia un proyecto rapidamente a travez de cdn y deja que vuesax haga la magia
          </div>
        </div>
      </codex>
      <codex titlex="Install en Proyecto NPM" doc="doc" :code="`
# npm
npm install vuesax


# yarn
yarn add vuesax
      `">
        <div slot="ejemplo">
          <div class="con-code">
            Para instalar Vuesax solo tiene que ejecutar este comando en su proyecto con npm y listo!
          </div>
        </div>
      </codex>
      <codex titlex="Uso" doc="doc" :code="`import Vue from 'vue'
import Vuesax from 'vuesax'

Vue.use(Vuesax)`">
        <div slot="ejemplo">
          <div class="con-code">
            Vuesax es una libreria de vue js para usarla hay que agregar el codigo:
          </div>
        </div>
      </codex>

      <codex titlex="Lusaxweb" doc="doc" no-codigo="true">
        <div slot="ejemplo">
          <div class="con-code">
            Esta libreria fue creada y es soportada por <a href="http://www.lusaxweb.com.ve/">Lusaxweb</a>
          </div>
        </div>
      </codex>


      </div>
    </con-doc>

  </div>
</template>

<script>

// import vsButton from '../../vuesax/components/vsBoton.vue'

import conDoc from '../../components/conDoc.vue'
import titlex from '../../components/title.vue'
import codex from '../../components/code.vue'
import $ from 'jquery'
export default {
  components:{
    conDoc,
    codex,
    // component
    // vsButton,
  },
  data(){
    return {
      code1:
      `
      <!DOCTYPE html>
    <html>
    <head>
      <link href="https://cdn.jsdelivr.net/npm/vuesax@2.0.9/dist/css/vuesax.css" rel="stylesheet">
      <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no, minimal-ui">
    </head>
    <body>
      <div id="app">
        <vs-button vs-type="primary-filled">Hola Mundo</vs-button>
      </div>

      <script src="https://unpkg.com/vue/dist/vue.js"><\/script>
      <script src="https://cdn.jsdelivr.net/npm/vuesax@2.0.9/dist/js/vuesax.js"><\/script>
      <script>
        new Vue({
          el: '#app'
        })
      <\/script>
    </body>
    </html>
      `,
    }
  },
  methods:{
    prueba(){
      console.log("paso");
    }
  }
}
</script>

<style lang="css" scoped>
  .h2-get {
    text-align: center;
    padding: 20px;
    color: rgba(0, 0, 0,.7);
    background: rgb(255, 255, 255);
  }
</style>



