<template>
  <div id="app">
    <div class="contenedor">
      <transition name="fade2">
      <router-view/>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
}
</script>

<style>
.fade2-enter-active, .fade2-leave-active {
  transition: all .5s;
}
.fade2-enter,.fade2-leave-to  /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translate(0,-100%);
}
.contenedor {
  margin-top: 48px;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  outline: none;
  text-transform: none;
  text-decoration: none;
}
*::-webkit-scrollbar{
  width: 5px;
}
*::-webkit-scrollbar-thumb{
  background: rgb(var(--uno));
  border-radius: 5px;
}
:root {
  --morado:92, 60, 196;
}
body {
  overflow-x: hidden;
  width: 100%;
}



</style>



